  top: 70px;
  right: -60px;
  background-color: #195557;
  color: #ffffff;
  padding: 34px 29px;
  z-index: 1;
  display: none;
  font-family: "Raleway", "Arial", sans-serif;
  font-feature-settings: 'pnum' on, 'lnum' on;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  text-transform: lowercase; }
  .gallery__item-info span {
    display: inline-flex;
    align-items: center;
    margin-right: 29px; }
  .gallery__item-info span:last-child {
    margin-right: 0; }
  .gallery__item-info span svg {
    order: -1;
    margin-right: 4px;
    fill: #ffffff; }
  .gallery__item-info::before {
    content: "";
    width: 30px;
    height: 18px;
    border-bottom: 9px solid #195557;
    border-top: 9px solid transparent;
    border-left: 15px solid transparent;
    border-right: 15px solid transparent;
    position: absolute;
    top: -18px;
    left: 0;
    right: 0;
    margin: 0 auto; }
  @media (max-width: 767px) {
    .gallery__item-info {
      display: block;
      padding: 10px;
      right: 11px;
      top: 4px;
      background-color: transparent;
      color: #195557;
      font-weight: 400;
      font-size: 10px;
      line-height: 16px; }
      .gallery__item-info::before {
        display: none; }
      .gallery__item-info span {
        margin-right: 17px; }
      .gallery__item-info span svg {
        width: 13px;
        height: 13px;
        fill: #195557;
        margin-right: 1px;
        margin-bottom: 3px; } }

.gallery__old-price,
.gallery__price {
  display: block; }

.gallery__old-price {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  text-decoration-line: line-through;
  color: #C2CCCA;
  margin: 0;
  padding: 0; }
  @media (max-width: 767px) {
    .gallery__old-price {
      font-size: 20px;
      line-height: 24px; } }

.gallery__price {
  font-weight: 700;
  font-size: 40px;
  line-height: 36px; }
  @media (max-width: 767px) {
    .gallery__price {
      font-size: 40px;
      line-height: 45px; } }

.gallery__button::after {
  content: "";
  width: 40px;
  height: 40px;
  background-size: 24px 24px; }

.gallery__button--prev::after {
  background: url("../img/svg/back.svg") no-repeat center; }

.gallery__button--next::after {
  background: url("../img/svg/next.svg") no-repeat center; }

@media (max-width: 767px) {
  .gallery__button {
    display: none; } }

.order {
  padding-top: 40px;
  text-align: center;
  padding-bottom: 48px; }
  @media (max-width: 767px) {
    .order {
      text-align: left;
      padding-top: 2px;
      padding-bottom: 41px; } }

@media (max-width: 767px) {
  .order__wrapper {
    padding: 0; } }

.order form {
  background-color: #EAEEEF;
  padding: 9px 49px 80px;
  border-radius: 30px;
  position: relative;
  margin-bottom: 70px; }
  @media (max-width: 767px) {
    .order form {
      padding: 11px 19px 8px;
      border-radius: 0;
      text-align: left;
      margin-bottom: 25px; } }

.order__title {
  font-weight: 700;
  font-size: 45px;
  line-height: 54px;
  margin: 30px 0;
  padding: 0; }
  @media (max-width: 767px) {
    .order__title {
      font-size: 24px;
      line-height: 29px;
      margin-bottom: 26px; } }

.order__menu {
  display: grid;
  grid-template-columns: 567px 1fr;
  gap: 78px;
  margin-bottom: 43px; }
  @media (max-width: 1499px) {
    .order__menu {
      grid-template-columns: 1fr 1fr;
      gap: 30px; } }
  @media (max-width: 1023px) {
    .order__menu {
      grid-template-columns: 1fr; } }
  @media (max-width: 767px) {
    .order__menu {
      margin-bottom: 31px; } }

.order fieldset {
  border: none;
  margin: 0;
  padding: 0; }

.order__left-column {
  display: grid;
  grid-template-columns: 1fr 1fr 199px;
  gap: 14px; }
  @media (max-width: 1499px) {
    .order__left-column {
      grid-template-columns: repeat(3, 1fr); } }
  @media (max-width: 767px) {
    .order__left-column {
      grid-template-columns: 1fr;
      gap: 15px; } }

.order__right-column {
  display: grid;
  grid-template-columns: 175px 1fr;
  column-gap: 25px;
  row-gap: 15px; }
  .order__right-column > * {
    align-self: flex-start; }
  @media (max-width: 767px) {
    .order__right-column {
      grid-template-columns: 1fr;
      grid-template-areas: "p0" "p2" "p1" "p3"; } }

fieldset.order__row {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 20px;
  margin: 20px 0; }
  @media (max-width: 1023px) {
    fieldset.order__row {
      grid-template-columns: 1fr; } }
  @media (max-width: 767px) {
    fieldset.order__row {
      gap: 10px;
      margin-bottom: 10px; } }

.order__submit-button {
  position: absolute;
  bottom: -34px;
  left: 0;
  right: 0;
  margin: 0 auto;
  max-width: 328px; }
  @media (max-width: 767px) {
    .order__submit-button {
      position: relative;
      bottom: auto;
      margin: 24px 0; } }

.order__balance {
  font-weight: 700;
  font-size: 24px;
  line-height: 29px;
  display: inline-flex;
  align-items: center;
  margin: 25px 0 10px; }
  .order__balance svg {
    width: 41px;
    height: 41px;
    margin-right: 10px; }
  @media (max-width: 767px) {
    .order__balance {
      padding: 41px 21px 2px;
      position: relative; }
      .order__balance svg {
        position: absolute;
        top: 0;
        left: 21px; }
      .order__balance span:last-child {
        display: block; } }

.order small {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 22px;
  text-align: center;
  font-feature-settings: 'pnum' on, 'lnum' on;
  display: inline-block;
  color: #195557;
  max-width: 195px; }
  @media (max-width: 767px) {
    .order small {
      text-align: left;
      padding-left: 21px; } }

